<template>
    <div class="admin_home pdlr16">
        <div class="adminHeader">
            <div class="adhSearch"><input type="text" placeholder="搜索"><button class="positTBCenter" type="button"><img src="@/assets/image/search.png" alt=""></button></div>
            <div class="adhNew positTBCenter"><a href="##">创建</a></div>
        </div>
        
        <div class="adhClassify mt10">
            <button type="button" @click="showClassify"><span>分类：</span><span>{{curType=='All'?'全部':(curType=='mini'?'小程序':curType)}}</span><img class="positTBCenter" src="@/assets/image/right.png" alt=""></button>
        </div>
        <div class="admin_list mt20">
            <ul>
                <li v-for="item in dataAll.article"><router-link :to="{path:'/admin_edit',query:{id:item.id}}">
                    <div class="admImg"><img :src="domain+item.thumb" alt=""><span>{{item.classify_tagname}}</span></div>
                    <div class="admCont">
                        <h4>{{item.title}}</h4>
                        <p>{{item.publish_time}}</p>
                    </div>
                </router-link><button type="button"><img src="@/assets/image/del.png" alt=""></button></li>
            </ul>
        </div>
        
        <popList :popDatas="popData" :popStatus="popStatus" @popChange="changClassify" @popConfirm="popConfirm"></popList>
    </div>
</template>
<script>
export default {
    name:'admin_home',
    data(){
        return{
            popStatus: false,
            popData:{
                title:'分类',
                slots: [
                    {
                    flex: 1,
                    values: [],
                    className: 'slot1',
                    textAlign: 'center',
                    valueKey:'2015-02'
                    }
                ],
            },
            tagArr:[],
            pages: 1,
            curType:'All',
            dataAll:[],//列表数据
            domain: this.hosts,
            isLoading: false,//是否加载中
        }
    },
    mounted(){
        console.log(this.popData.slots);
        var _this = this;
        this.requests({reqUrl:'/api/index/classify_tags'}).then(function(res){
            console.log(res);
            var curdata = [];
            curdata[0] ='全部';
            var tagArr=[];
            tagArr[0] = 'All';
            for(var i=0;i<res.data.length; i++){
                curdata[i+1] = res.data[i].tags_name;
                tagArr[i+1] = res.data[i].tags;
            }
            _this.popData.slots[0].values = curdata;
            _this.tagArr =tagArr;
            console.log(tagArr);
        })
        this.ajax_req(1,'All');
        console.log(this.hosts);
        this.scrolls();
    },
     methods: {
        showClassify:function(){
            this.popStatus?(this.popStatus = false):(this.popStatus = true);
        },
        changClassify:function(e){
            // 选择分类
            console.log(e);
        },
        popConfirm(e){
            // 确定分类
            console.log(e);
            this.curType = e.curValue[0];
            console.log(e.curValue[0]);
            var curTypes;
            for(var i=0; i<this.tagArr.length;i++){
                if(e.curValue[0] =='全部'){
                    curTypes = 'All';
                }else if(e.curValue[0] =='小程序'){
                    curTypes = 'mini';
                }else if(e.curValue[0]==this.tagArr[i]){
                    curTypes = e.curValue[0];
                }
            }
            this.curType = curTypes;
            this.ajax_req(1,curTypes);
            this.popStatus = false;
        },
        ajax_req:function(pages,artType){
            //  获取文章列表
            var _this = this;
            if(!this.isLoading){
                this.isLoading = true;
                if(pages<=this.dataAll.allpage||pages==1){
                    this.requests({reqUrl:'/api/index/get_article?page_num='+pages+'&select_value='+artType}).then(function(ret){
                        if (ret) {
                            if(pages>1){
                                var oldList = _this.dataAll.article;
                                var curDatas = ret;
                                if(ret.article.length>0){
                                    for(var i=0;i<ret.article.length;i++){
                                        oldList.push(ret.article[i]);
                                    }
                                    curDatas.article = oldList;
                                    _this.dataAll = curDatas;
                                }
                            }else{
                                _this.dataAll = ret;
                            }
                            _this.pages = pages;
                        }
                         _this.isLoading = false;
                    })
                }else{
                    MintUI.Toast({
                        message: '没有更多了！',
                        position: 'middle',
                        duration: 3000
                    })
                    _this.isLoading = false;
                }
            }
           
        },
        scrolls:function(){
            // 加载更多
            var _this = this;
            api.addEventListener({
                name:'scrolltobottom',
                extra:{
                    threshold: 0           
                }
            }, function(ret, err){
                var reqPage = ++ _this.curPages;
                _this.ajax_req(reqPage,this.curType);
            });
        },
     },
     
}
</script>
<style>
    
    .admin_home img{
        display: block;
    }
    .adminHeader{
        position: relative;
        padding-right: 80px;
        padding-top: 10px;
        border-bottom: 1px solid rgb(230, 230, 230);
    }
    .adhSearch{
        position: relative;
        padding-left: 40px;
        
    }
    .adhSearch input{
        height: 40px;
        line-height: 40px;
        width: 100%;
        display: block;
    }
    .adhSearch button{
        left: 0;
    }
    .adhNew{
        right: 0;
    }
    .adhNew a{
        display: block;
        padding: 0px 16px;
        border-radius: 4px;
        line-height: 30px;
        background-color: rgb(18, 150, 219);
        color: white;
    }
    .adhSearch button img{
    }
    .adhClassify {
        border: 1px solid rgb(240, 240, 240);
        position: relative;
        border-radius: 4px;
    }
    .adhClassify button{
       width: 100%;
       display: block;
       line-height: 50px;
       text-align: left;
       position: relative;
       padding: 0px 16px;
    }
    .adhClassify img{
        right: 16px;
        width: 16px;
        height: 16px;
    }
    .adhClList{
        position: absolute;
        top: 50px;
        left: 0;
        width: 100%;
        z-index: 100;
        background-color: white;
        box-shadow: 0px 0px 10px 0px rgb(200, 200, 200);
        padding: 16px;
        border-radius: 4px;
        display: none;
    }
    .adhClList li{
        line-height: 40px;
        border-bottom: 1px solid rgb(230, 230, 230);
    }
    .adhClList ul{
        max-height: 300px;
        overflow-y: auto;
    }
    
    .admin_list li{
        position: relative;
        padding: 16px 0px;
        padding-right: 50px;
        border-bottom: 1px solid rgb(230, 230, 230);
    }
    .admin_list a{
        display: block;
        position: relative;
        padding-left: 140px;
    }
    .admImg{
        position: absolute;
        top: 50%;
        left: 0;
        transform: translateY(-50%);
    }
    .admImg img{
        width: 120px;
        height: 100px;
        object-fit: cover;
        border-radius: 4px;
    }
    .admImg span{
        position: absolute;
        top: 0;
        left: 0;
        background-color: rgb(18, 150, 219);
        color: white;
        padding: 2px 6px;
        z-index: 101;
    }
    .admCont{
        min-height: 100px;
        position: relative;
        padding-bottom: 20px;
    }
    .admCont h4{
        font-size: 14px;
        margin-bottom: 10px;
    }
    .admCont p{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        font-size: 12px;
        color: rgb(153, 153, 153);
    }
    .admin_list li button{
        position: absolute;
        top: 50%;
        right: 0;
        transform: translateY(-50%);
        border: none;
        background-color:transparent;
    }
    .admin_list li button img{
        width: 30px;
        height: 30px;
    }
    
</style>


